.App {
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.App-header {
  background-color: #282c34;
  /*min-height: 100vh;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-top: 2em;
  max-width: 900px;
}
.ingress {
  background: rgba(255, 255, 255, 0.9);
  color: #3f3f3f;
  width: 100%;
}

.ingress p,
.normal-output p,
.exceptional-output p {
  padding-left: 2em;
  padding-right: 2em;
}

.normal-output {
  background: rgba(255, 255, 255, 0.9);
  color: #3f3f3f;
  width: 100%;
}
.basic-info {
  text-align: left;
  padding-bottom: 1em;
}

.App-main {
  background-color: #fff;
  /*min-height: 100vh;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #383838;
  padding-bottom: 2em;
  width: 100%;
  max-width: 900px;
  box-sizing: border-box;
  border-style: solid;
  border-color: #282c34;
  border-width: 2px;
}

.App-link {
  color: #61dafb;
}
